.before-after {
  width: 320px;
  margin-left: -20px;
  background-color: $basic_grey-3;
}

.before-after__wrapper {
  padding: 0 20px;
}

.before-after__title {
  padding-top: 27px;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}

.fieldset__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.before-after__text {
  margin-bottom: 25px;
  font-family: 'Arial';
  font-size: 14px;
  line-height: 18px;
}

.before-after__result-box {
  width: 124px;
  border: 1px solid $special_grey;
  transform: rotate(180deg);
}

.before-after__result-title {
  margin: 0;
  height: min-content;
  text-align: center;
  transform: rotate(180deg);
  text-transform: uppercase;
  font-size: 24px;
    line-height: 24px;
}

.before-after__result-text {
  display: inline-block;
  padding: 0 10px;
  text-transform: lowercase;
  transform: rotate(180deg);
  text-align: center;
  font-family: 'Arial';
  font-size: 12px;
  line-height: 12px;
}


.before-after__cost {
  margin-bottom: 42px;
  text-align: center;
  font-size: 14px;
  text-transform: uppercase;
}

.before-after--image{
  position: relative;
}

.range-controls__wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  align-content: center;
  text-transform: uppercase;
  font-size: 16px;
}

.range-controls {
  position: relative;
  background-color: $basic_grey;
  width: 84px;
  height: 14px;
  border: 1px solid $special_grey;
  border-radius: 6px;
  padding: 0 6px;
}

.range-controls .bar {
  // position: absolute;
  margin-top: 3px;
  height: 6px;
  border-radius: 6px;
  background: $basic-green;
}
